import React from 'react'
import { Container } from '../Categoria/CategoriaElements'
import { Layout } from "../Layout/Layout"
import { Row, Col } from "react-bootstrap"
import item1 from '../../img/item1.jpg'
import item2 from '../../img/item2.jpg'
import item3 from '../../img/item3.jpg'
import item4 from '../../img/item4.jpg'
import item5 from '../../img/item5.jpg'


export const Categoria = () => {
    return (
        <Container>
            <Layout>
                <h1>Categoria</h1>
                <hr/>
                <Row>
                    <Col xm={12} md={6} className="coluna">
                        <img src={item1} alt="" className="img-left"/>
                        <button>Mochilas</button>
                    </Col>

                    <Col xm={12} md={6}>
                        <Row>
                            <Col xm={12} md={6} className="coluna">
                                <img src={item2} alt="" />
                                <button>Sapatos</button>
                            </Col>
                            <Col xm={12} md={6} className="coluna">
                                <img src={item3} alt="" />
                                <button>Bolsas</button>
                            </Col>
                            <Col xm={12} md={6} className="coluna">
                                <img src={item4} alt="" />
                                <button>Bonés</button>
                            </Col>
                            <Col xm={12} md={6} className="coluna">
                                <img src={item5} alt="" />
                                <button>Relógios</button>
                            </Col>
                        </Row>
                    </Col>

                </Row>

            </Layout>
        </Container>
    )
}
