import styled from 'styled-components'
import banner from "../../img/slide2.jpg"

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    margin-bottom: 70px;

    h1{
        font-size: clamp(3rem, 3vw, 4rem);
        font-family:${props => props.theme.font_secundaria};
        margin: 20px auto;
        text-transform: uppercase;
        text-align: center;
        margin-top: 60px;
    }
    hr{
        width: 20%;
        margin: 0 auto;
        margin-bottom: 30px;
    }
`

export const WrapBanner = styled.div`
    height: 100vh;
    position: relative;

    .parallax {
        background-image: url(${banner});
        min-height: 100vh; 
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .innertxt{
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding-top: 400px;
        text-align: center;
        background: rgba(0, 0, 0, .5);
        
        h5{
            cursor: pointer;
            width: fit-content;
            margin: 0 auto;
            font-size: clamp(1.2rem, 4vw, 2rem);
            color: #ff0000;
            text-transform: uppercase;
            background: #fff;
            padding: 10px 20px;
            border-radius: 50px;
            margin-bottom: 20px;

            &:hover{
                background: red;
                color: #fff;
            }
        }
        
        p{
            font-size: clamp(1.5rem, 2.5vw, 1.7rem);
            color: #fff;
            height:28px;
        }

    }
`



export const InfoContato = styled.div`
      width: 100%;
      height: fit-content;

    .sub-txt{
        text-align: center;
        margin-bottom: 70px;
    }

    .coluna{
        display: flex;
        margin-bottom: 20px;
        .inner-coluna{
            .icone{
                font-size: 2rem;
                color: red;
            }
            .p-bold{
                 font-weight: bold;
            }
            .mail{
                color: #0caa0c;
            }
        }

    }
`

export const Form = styled.div`
      width: 100%;
      height: fit-content;

      form{
          width: 95%;
          height: fit-content;
          margin: 0 auto;
          padding: 50px 0;
          border: 1px solid red;
          border-radius: 5px; 
          margin-top: 80px;

          @media (min-width: 950px){
              width: 80%;
          }

          input{
              display: block;
              width: 95%;
              height: 55px;
              padding-left: 15px;
              margin: 0 auto;
              margin-bottom: 20px;
              border-radius: 5px;
              border: 1px solid red;
              outline: none;
              
          }

          textarea{
              display: block;
              width: 95%;
              padding-left: 15px;
              padding-top: 20px;
              margin: 0 auto;
              border-radius: 5px;
              border: none;
              outline: none;
              border: 1px solid red;
          }
        
        button{
            display: block;
            padding: 10px 50px;
            border-radius: 5px;
            border: 1px solid red;
            margin: 0 auto;
            margin-top: 30px;
            background: red;
            color: #fff;
            outline: none;

            &:hover{
                background: #fff;
                color: red; 
            }
        }
      }

`