import styled from "styled-components"


export const Container = styled.div`
    width: 100%;
    text-align: center;
    padding: 0 20px;

    h1{
        font-size: clamp(3rem, 3vw, 4rem);
        font-family:${props => props.theme.font_secundaria};
        margin: 20px auto;
        text-transform: uppercase;
    }
    hr{
        width: 20%;
        margin: 0 auto;
        margin-bottom: 30px;
    }

    h3{
        font-size: clamp(1.5rem, 3vw, 2rem);
        color: #E32B2B;
    }

    .owl-theme{
        text-align: center;
        outline: none;

        .item{
            width: 100%;
            cursor: pointer;


            &:hover > .shop-icon{
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .shop-icon{
                cursor: pointer;;
                display: none;
                width: 15%;
                height: 12%;
                background: #333;
                position: absolute;
                bottom: 75px;

                font-size: clamp(1rem, 2.5vw, 1.5rem);
                color: #fff;

                &:hover{
                    background: #E32B2B;
                }
            }
            h2, p{
                width: 80%;
                margin: 10px auto;
                font-size: clamp(1rem, 2.5vw, 1rem);
            }
            h2{
                color: #E32B2B;
                font-size: clamp(1rem, 2.5vw, 1.4rem);
            }
            .underline-price{
                text-decoration:line-through;
                margin-right: 15px;
            }
            .price{
                background: #E32B2B;
                color: #fff;
                padding: 5px 10px;
                border-radius: 20px;
            }

        }
    }
`