import styled from "styled-components";

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    padding: 70px 0;

    h1{
        font-size: clamp(3rem, 3vw, 4rem);
        font-family:${props => props.theme.font_secundaria};
        margin: 20px auto;
        text-transform: uppercase;
        text-align: center;
    }
    hr{
        width: 20%;
        margin: 0 auto;
        margin-bottom: 30px;
    }
    
    .coluna{
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover{
            transform: scale(1.1);
        }
        

            &:hover>button{
                background: #333;
                color: #fff;
                border: none;
            }

        .img-left{
            height: 100%;
            height: 95%;
            object-fit: cover;
        }
        img{
            cursor: pointer;
            width: 100%;
            margin-top: 50px;
            object-fit: cover;
        }
        button{
            outline: none;
            position: absolute;
            top: 90%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: clamp(1rem, 2.5vw, 1.3rem);
            background: #fff;
            padding: 3px 15px;
            border-radius: 100px;
            border: none;
            border: 1px solid #dbdbdb;

            &:hover{
                background: #333;
                color: #fff;
                border: none;
            }


        }
    }
`