import React from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import novidade1 from '../../img/novidade1.jpg'
import novidade2 from '../../img/novidade2.jpg'
import novidade3 from '../../img/novidade3.jpg'
import novidade4 from '../../img/novidade4.jpg'
import novidade5 from '../../img/novidade5.jpg'
import novidade6 from '../../img/novidade6.jpg'
import { Container } from "./SliderOwlElements.js"
import { FaShoppingCart } from "react-icons/fa"

const options = {
    margin: 10,
    responsiveClass: true,
    nav: true,
    dots: true,
    autoplay: false,
    smartSpeed: 200,
    responsive: {
        0: {
            items: 1,
        },
        400: {
            items: 1,
        },
        600: {
            items: 2,
        },
        700: {
            items: 2,
        },
        800: {
            items: 3,
        },
        1000: {
            items: 4,

        }
    },
};

export const OwlSlider = () => {
    return (
        <Container>

            <h1>Novidade do mês</h1>
            <hr />
            <h3>Aqui você encontra tudo de novo no mundo da moda.</h3>

            <OwlCarousel style={{ marginTop: "80px" }} className="owl-theme" loop {...options}>
                <div className="item">
                    <img src={novidade1} alt="novidade StudioX" />
                    <div className="shop-icon">
                        <FaShoppingCart/>
                    </div>
                    <h2>Boné</h2>
                    <p><span className="underline-price">R$ 10,00</span> <span className='price'>R$ 15,00</span></p>
                </div>

                <div class="item">
                    <img src={novidade2} alt="novidade StudioX" />
                    <div className="shop-icon">
                        <FaShoppingCart/>
                    </div>
                    <h2>Relógio</h2>
                    <p><span className="underline-price">R$ 10,00</span> <span className='price'>R$ 15,00</span></p>
                </div>

                <div class="item">
                    <img src={novidade3} alt="novidade StudioX" />
                    <div className="shop-icon">
                        <FaShoppingCart/>
                    </div>
                    <h2>Óculos</h2>
                    <p><span className="underline-price">R$ 10,00</span> <span className='price'>R$ 15,00</span></p>
                </div>

                <div class="item">
                    <img src={novidade4} alt="novidade StudioX" />
                    <div className="shop-icon">
                        <FaShoppingCart/>
                    </div>
                    <h2>Carteira</h2>
                    <p><span className="underline-price">R$ 10,00</span> <span className='price'>R$ 15,00</span></p>
                </div>

                <div class="item">
                    <img src={novidade5} alt="novidade StudioX" />
                    <div className="shop-icon">
                        <FaShoppingCart/>
                    </div>
                    <h2>T-shirt</h2>
                    <p><span className="underline-price">R$ 10,00</span> <span className='price'>R$ 15,00</span></p>
                </div>

                <div class="item">
                    <img src={novidade6} alt="novidade StudioX" />
                    <div className="shop-icon">
                        <FaShoppingCart/>
                    </div>
                    <h2>Camisa Social</h2>
                    <p><span className="underline-price">R$ 10,00</span> <span className='price'>R$ 15,00</span></p>
                </div>
            </OwlCarousel>
        </Container>
    )
}