import React from 'react'
import { WrapBanner } from "./PromocaoElements"

export const Promocao = () => {
    return (
        <WrapBanner>
            <div class="parallax"></div>

            <div className="innertxt">
                <h5>Final de promoção do mês</h5>
                <h6>50% de desconto em todas as peças</h6>
                <button>Saiba mais</button>
            </div>
        </WrapBanner>
    )
}
