import React from 'react'
import { Container, WrapBanner, InfoContato, Form } from './ContatoElements'
import { Row, Col } from "react-bootstrap"
import { IoClose } from "react-icons/io5";
import { Layout } from "../../Components/Layout/Layout"

export const Contato = () => {
    return (
        <Container>
            <WrapBanner>
                <div class="parallax"></div>

                <div className="innertxt">
                    <h5>Nosso Escritório</h5>
                    <p>Travessa São Luis, 2320</p>
                    <p>Codó, MA - Brasil</p>
                    <p>Telefone: (99) 98144 - 4269</p>
                </div>
            </WrapBanner>
            <Layout>

                <InfoContato>
                    <h1>Nos ache aqui</h1>
                    <hr/>
                    <p className="sub-txt">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequatur, earum vero error iusto labore officiis repellendus aliquid optio nesciunt est.</p>


                    <Row>
                        <Col xm={12} md={6} className="coluna">
                            <div className="inner-coluna">
                                <IoClose className="icone"/>
                            </div>

                            <div className="inner-coluna">
                                <p className="p-bold">Telefone:</p>
                                <p>(99) 98144 - 4269</p>
                            </div>
                        </Col>

                        <Col xm={12} md={6} className="coluna">
                            <div className="inner-coluna">
                                <IoClose className="icone"/>
                            </div>

                            <div className="inner-coluna">
                                <p className="p-bold">Fax:</p>
                                <p>(99) 98144 - 4269</p>
                            </div>
                        </Col>


                        <Col xm={12} md={6} className="coluna">
                            <div className="inner-coluna">
                                <IoClose className="icone"/>
                            </div>

                            <div className="inner-coluna">
                                <p className="p-bold">Web:</p>
                                <p className="mail">www.example.com</p>
                            </div>
                        </Col>

                        <Col xm={12} md={6} className="coluna">
                            <div className="inner-coluna">
                                <IoClose className="icone"/>
                            </div>

                            <div className="inner-coluna">
                                <p className="p-bold">E-mail:</p>
                                <p className="mail">office@example.com</p>
                            </div>
                        </Col>

                    </Row>
                </InfoContato>

        
                <Form>
                    <h1>Contato</h1>
                    <hr/>
                    <form action="">
                        <input type="text" name="texto" id="texto" placeholder="Seu Nome"/>
                        <input type="email" name="email" id="email" placeholder="Seu E-mail"/>
                        <input type="text" name="assunto" id="assunto" placeholder="Assunto"/>
                        <textarea name="mensagem" id="mensagem" cols="30" rows="10" placeholder="Sua Mensagem"></textarea>

                        <button type="submit">Enviar</button>
                    </form>
                </Form>
            </Layout>
        </Container>
    )
}
