import React from 'react'
import { Container } from "./FooterElements"
import { Row, Col } from "react-bootstrap"
import { Layout } from "../Layout/Layout"
import { Link } from "react-router-dom"

export const Footer = () => {
    return (
        <Container>
            <Layout>
                <Row>
                    <Col xm={12} md={4} className="coluna">
                        <h2>Sobre</h2>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quisquam tempore id nulla inventore numquam. Placeat quas temporibus omnis hic in.</p>
                        <button> Like us on facebook</button>
                    </Col>

                    <Col xm={12} md={4} className="coluna coluna-links">
                        <nav>
                            <h2>Links</h2>
                            <ul>
                                <li><Link to="/" className="Link" >Sobre</Link></li>
                                <li><Link to="/" className="Link">Lançamentos</Link></li>
                                <li><Link to="/" className="Link">Carreira</Link></li>
                                <li><Link to="/" className="Link">Serviços</Link></li>
                                <li><Link to="/" className="Link">Projetos</Link></li>

                            </ul>
                        </nav>
                    </Col>

                    <Col xm={12} md={4} className="coluna">
                        <h2>Contato</h2>
                        <p>Travessa São Luis, 8520</p>
                        <p>Telefone: (99) 98144 - 4269</p>
                        <p>Email: flanelon18@gmail.com</p>
                    </Col>

                    
                    <Col xm={12} className="coluna">
                        <hr/>
                        <h3>© Copyright 2016 by Sphene. All Rights Reserved.</h3>
                    </Col>
                </Row>
            </Layout>
        </Container>
    )
}
