import styled from "styled-components"

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    background: #202020;
    color: #fff;
    padding-top: 80px;

    .coluna{
        margin-bottom: 50px;
        h2{
        font-size: clamp(1.5rem, 2.5vw, 1.7rem);
        margin-bottom: 20px;
        }
        p{
            color: #9b9b9b;
        }
        button{
            padding: 10px 40px;
            background: #11117e;
            color: #fff;
            border-radius: 50px;
            border: none;

            &:hover{
                background: #fff;
                color: #11117e;
            }
        }
        nav{
            ul{
                list-style: none;
                li{
                    height: 32px;
                    .Link{
                        text-decoration:none;
                        color: #9b9b9b;
                        &:hover{
                            padding-left: 8px;
                            color: #fff;
                            transition: all .4s;
                        }
                    }
                }
            }
        }
        hr{
            border-top: 1px solid #fff;
        }
        
        h3{
            text-align: center;
            font-size: clamp(1.2rem, 2.5vw, 1.2rem);
        }
    }
    .coluna-links{

        @media (min-width: 956px){
            padding-left: 80px;
        }
    }

`;