import styled from "styled-components"
import banner from "../../img/slide2.jpg"

export const WrapBanner = styled.div`
    height: 50vh;
    position: relative;

    .parallax {
        background-image: url(${banner});
        min-height: 50vh; 
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .innertxt{
        width: 100%;
        height: 50vh;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding-top: 130px;
        text-align: center;
        background: rgba(0, 0, 0, .5);
        
        h5{
            width: fit-content;
            margin: 0 auto;
            font-size: clamp(1.2rem, 4vw,3rem);
            color: #ff0000;
            text-transform: uppercase;
            background: #fff;
            padding: 10px 20px;
            border-radius: 50px;
        }

        h6{
            font-size: clamp(1rem, 4vw,2.5rem);
            color: #fff;
            text-transform: uppercase;
            margin: 10px auto;
        }
        button{
            font-size: clamp(1rem, 2.5vw,1.5rem);
            padding: 10px 30px;
            border-radius: 50px;
            margin-top: 15px;
            background: #E32B2B;
            color: #fff;
            border: 2px solid #fff;

            &:hover{
                background: transparent;
                color: #fff;
                border: 2px solid ;
            }
        }

    }
`