import styled from 'styled-components'

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    background:#F8F8F8;
    margin-top: 80px;
    padding-bottom: 50px;


    h1{
        width: 95%;
        height: 70px;
        margin-top: 50px;
        margin-left: 10px;
        font-size: clamp(1.5rem, 2.5vw, 2rem);
        border-bottom: 1px solid #CDCDCD;
    }

    .items{
        width: 100%;
        display: flex;
        padding: 20px 20px;
        .img-box{
            width: 40%;
            background-color: red;
            img{
                width: 100%;
                cursor: pointer;
            }
        }

        .txt-box{
            width: 60%;
            padding-left: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            h2{
                font-size: clamp(1.2rem, 2.5vw, 1.2rem);
                cursor: pointer;
                &:hover{
                    color: #7a7a7a;
                }
            }
            h3{
                font-size: clamp(1.2rem, 2.5vw, 1.2rem);
                color: #858585;
            }
        }
    }
`